<template>
  <div class="login">
    <!-- refmodel -->
    <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form">
      <h3 class="title"></h3>
      <el-form-item prop="username">
        <el-input v-model="loginForm.username" type="text" auto-complete="off" placeholder="">
          <svg-icon slot="prefix" icon-class="user" class="el-input__icon input-icon" />
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <!-- .native -->
        <el-input
          v-model="loginForm.passwd"
          type="password"
          auto-complete="off"
          placeholder=""
          @keyup.enter.native="handleLogin"
        >
          <svg-icon slot="prefix" icon-class="password" class="el-input__icon input-icon" />
        </el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input
          v-model="loginForm.code"
          auto-complete="off"
          placeholder=""
          style="width: 63%"
          @keyup.enter.native="handleLogin"
        >
          <svg-icon slot="prefix" icon-class="validCode" class="el-input__icon input-icon" />
        </el-input>
        <div class="login-code">
          <img :src="codeUrl" @click="getCode" class="login-code-img"/>
        </div>
      </el-form-item>
      <el-checkbox v-model="loginForm.rememberMe" style="margin:0px 0px 25px 0px;"></el-checkbox>
      <el-form-item style="width:100%;">
        <el-button
          :loading="loading"
          size="medium"
          type="primary"
          style="width:100%;"
          @click.native.prevent="handleLogin"
        >
          <span v-if="!loading"> </span>
          <span v-else>  ...</span>
        </el-button>
        <el-button @click="resetForm"></el-button>
      </el-form-item>
    </el-form>
    <!--    -->
    <div class="el-login-footer">
      <span>Copyright  2018-2020 ruoyi.vip All Rights Reserved.</span>
    </div>
  </div>
</template>

<script>
import {getCodeImg} from '@/api/login'
import Cookies from 'js-cookie'
import {encrypt, decrypt} from '@/utils/jsencrypt'

export default {
  name: 'Login',
  data(){
    return {
      codeUrl: '', //url
      loginForm: {
        username: 'admin',
        passwd: 'admin123',
        rememberMe: true,
        code: '',
        uuid: '',
      },
      loginRules: { //prop
        username: [
          {required: true, trigger: 'blur', message: "" }
        ],
        password: [
          { required: true, trigger: "blur", message: "" }
        ],
        code: [{ required: false, trigger: "change", message: "" }]
      },
      loading: false,
      redirect: undefined
    }
  },
  watch: { //
    $route: {
      handler: function(route){
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  created(){ //cookie
    this.getCode()
    this.getCookie()
    console.log('this.$route', this.$route)
    this.redirect = this.$route.query && this.$route.query.redirect
  },
  methods: {
    getCode(){ //
      getCodeImg().then(res => { //
        this.codeUrl = "data:image/gif;base64," + res.img;
        this.loginForm.uuid = res.uuid;
      })
    },
    getCookie(){ //cookies
      const passwd = Cookies.get('passwd')
      this.loginForm = {
        username: Cookies.get('username') || this.loginForm.username,
        passwd: passwd ? decrypt(passwd) : this.loginForm.passwd,
        rememberMe: Cookies.get('rememberMe') ? true : false
      }
    },
    handleLogin(){ //
      this.$refs.loginForm.validate(valid => { //el-form
        if(valid){ //
          this.loading = true //loading
          if(this.rememberMe){ //cookie
            Cookies.set('username', this.username)
            Cookies.set('passwd', this.passwd)
            Cookies.set('rememberMe', this.rememberMe)
          }
          else{ //
            Cookies.remove('username')
            Cookies.remove('passwd')
            Cookies.remove('rememberMe')
          }
          this.$store //Login
            .dispatch('Login', this.loginForm)
            .then(() => { //Login
              this.$route.push({path: this.redirect || '/'}) //
            })
            .catch(() => { //
              this.loading = false
              this.getCode()
            })
        }
      })
    },
    resetForm(){ //
      this.$refs.loginForm.resetFields()
    }
  }
}
</script>


<style rel="stylesheet/scss" lang="scss">
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: url("~@/assets/images/login-background.jpg");
  background-size: cover;
}
.title {
  margin: 0px auto 30px auto;
  text-align: center;
  color: #707070;
}

.login-form {
  border-radius: 6px;
  background: #ffffff;
  width: 400px;
  padding: 25px 25px 5px 25px;
  .el-input {
    height: 38px;
    input {
      height: 38px;
    }
  }
  .input-icon {
    height: 39px;
    width: 14px;
    margin-left: 2px;
  }
}
.login-tip {
  font-size: 13px;
  text-align: center;
  color: #bfbfbf;
}
.login-code {
  width: 33%;
  height: 38px;
  float: right;
  img {
    cursor: pointer;
    vertical-align: middle;
  }
}
.el-login-footer {
  height: 40px;
  line-height: 40px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  font-family: Arial;
  font-size: 12px;
  letter-spacing: 1px;
}
.login-code-img {
  height: 38px;
}
</style>
